import { onWindowResize } from 'utils';
import Swiper, { Pagination } from 'swiper';
import buildSliders from './buildSliders';

Swiper.use([Pagination]);

const ToolsSlider = () => {
	const sliderClass = '.toolsSlider';
	buildSliders(sliderClass);

	let slideEl = document.querySelectorAll(sliderClass);

	if (typeof (slideEl) !== 'undefined' && slideEl != null) {
		let toolsSliderEl;

		const enableSlider = () => {
			const $currentSlideIndex = document.querySelector('.toolsCurrent');
			const $totalSlideIndex = document.querySelector('.toolsTotal');

			toolsSliderEl = new Swiper(sliderClass, {
				observer: true,
				observeParents: true,
				speed: 800,
				slidesPerView: 2,

				pagination: {
					el: '.toolsProgress',
					type: 'progressbar',
					clickable: true,
				},

				on: {
					init: (slider) => {
						const index = slider.slides.length;
						$totalSlideIndex.innerHTML = index >= 10 ? index : `0${index}`;
					},
					slideChange: (slider) => {
						const index = slider.realIndex + 2;
						$currentSlideIndex.innerHTML = index >= 10 ? index : `0${index}`;
					},
				},
			});
		};

		const checkWindowWidth = () => {
			if (window.innerWidth < 768) {
				slideEl.forEach(element => {
					if (!element.classList.contains('swiper-initialized')) enableSlider();
				});
			} else {
				slideEl.forEach(element => {
					if (element.classList.contains('swiper-initialized')) {
						toolsSliderEl.destroy(true, true);
					}
				});
			}
		};

		checkWindowWidth();

		onWindowResize(checkWindowWidth);
	}
};

export default ToolsSlider;
