// import 'ScssComponents/_showcase_hero.scss';
import gsap from 'gsap';
import Observer from 'gsap/Observer';
import ScrollTrigger from 'gsap/ScrollTrigger';
import Swiper, { Pagination, EffectFade } from 'swiper';
import buildSliders from './buildSliders';

function ShowcaseHero() {
	const $hero = document.querySelector('.showcaseHero');

	if (!$hero) return null;

	gsap.registerPlugin(Observer);
	gsap.registerPlugin(ScrollTrigger);

	const $bg = document.querySelector('.showcaseHeroBg');
	const $controls = document.querySelector('.showcaseHeroControls');
	const $content = document.querySelector('.showcaseHeroContent');
	// const $image = document.querySelector('.showcase_hero__slider_item_in');

	const tl = gsap.timeline({
		paused: false,
		delay: 1,
	});

	const initAnim = () => {
		const $image = document.querySelector('.swiper-slide-active .showcase_hero__slider_item_in');

		setTimeout(() => {
			tl
				.addLabel('start')
				.fromTo($content, {
					opacity: 0,
					y: 50,
				}, {
					opacity: 1,
					y: 0,
					ease: 'power2.out',
				}, 'start')
				.fromTo($controls, {
					opacity: 0,
				}, {
					opacity: 1,
					ease: 'power2.out',
				}, 'start')
				.fromTo($bg, {
					yPercent: -100,
					opacity: 0,
				}, {
					yPercent: 0,
					opacity: 1,
					ease: 'power2.out',
				}, 'start')
				.fromTo($image, {
					opacity: 0,
					xPercent: 30,
				}, {
					opacity: 1,
					xPercent: 0,
					ease: 'power2.out',
				}, '-=.2');
		}, 600);
	};

	const sliderClass = '.showcaseHeroSlider';
	const $totalSlideIndex = document.querySelectorAll('.showcaseHeroTotal');
	const $currentSlideIndex = document.querySelector('.showcaseHeroCurrent');
	const $prevBtn = document.querySelector('.showcaseHeroPrev');
	const $nextBtn = document.querySelector('.showcaseHeroNext');

	buildSliders(sliderClass);
	Swiper.use([Pagination, EffectFade]);

	let $slideEl = document.querySelectorAll(sliderClass);
	const $slideImages = document.querySelectorAll('.showcase_hero__slider_item_in');
	let currentIndex = 0;
	let sliderLength = $slideImages.length;
	const duration = 0.4;
	const AUTOPLAY_INTERVAL = 3000;
	let isAnimationActive = false;
	let autoPlayInterval = null;

	if (typeof ($slideEl) !== 'undefined' && $slideEl != null) {
		let sliderEl = new Swiper(sliderClass, {
			observer: true,
			observeParents: true,
			speed: 600,
			slidesPerView: 1,
			effect: 'fade',
			fadeEffect: { crossFade: true },
			allowTouchMove: false,
			centeredSlides: false,
			pagination: {
				el: '.showcase_hero__progress_dots',
				type: 'progressbar',
			},
			on: {
				afterInit: (slider) => {
					const index = slider.slides.length;
					$totalSlideIndex.innerHTML = index;
					initAnim();
				},
				slideChange: (slider) => {
					const { realIndex } = slider;
					$currentSlideIndex.innerHTML = realIndex + 1;
					currentIndex = realIndex;
				},
			},
		});
		const outAnimation = (index) => {
			gsap.fromTo($slideImages[index], {
				opacity: 1,
				xPercent: 0,
			}, {
				opacity: 0,
				xPercent: -15,
				duration,
				ease: 'power2.out',
				onStart: () => {
					isAnimationActive = true;
				},
			});
		};

		const inAnimation = (index) => {
			gsap.fromTo($slideImages[index], {
				opacity: 0,
				xPercent: 0,
			}, {
				opacity: 1,
				xPercent: 0,
				duration: duration * 1.5,
				ease: 'power2.in',
				onComplete: () => {
					setAutoplayInterval();
					isAnimationActive = false;
				},
			});
		};

		const clearAutoplayInterval = () => {
			clearInterval(autoPlayInterval);
		};

		const prevSlideHandler = () => {
			if (isAnimationActive) return;
			clearAutoplayInterval();
			outAnimation(currentIndex);
			setTimeout(() => {
				if (currentIndex === 0) {
					sliderEl.slideTo(sliderLength - 1);
					inAnimation(currentIndex);
				} else {
					sliderEl.slidePrev();
					inAnimation(currentIndex);
				}
			}, duration * 1000);
		};

		const nextSlideHandler = () => {
			if (isAnimationActive) return;
			clearAutoplayInterval();
			outAnimation(currentIndex);
			setTimeout(() => {
				if (currentIndex === sliderLength - 1) {
					sliderEl.slideTo(0);
					inAnimation(currentIndex);
				} else {
					sliderEl.slideNext();
					inAnimation(currentIndex);
				}
			}, duration * 1000 * 0.7);
		};

		const setAutoplayInterval = () => {
			autoPlayInterval = setInterval(() => {
				nextSlideHandler();
			}, AUTOPLAY_INTERVAL);
		};

		$prevBtn.addEventListener('click', () => {
			prevSlideHandler();
		});

		$nextBtn.addEventListener('click', () => {
			nextSlideHandler();
		});

		Observer.create({
			target: $slideEl,
			type: 'touch',
			tolerance: 50,
			onLeft: () => nextSlideHandler(),
			onRight: () => prevSlideHandler(),
		});

		const onLeave = () => {
			clearAutoplayInterval();
		};

		const onEnter = () => {
			setAutoplayInterval();
		};

		const onViewHandler = () => {
			ScrollTrigger.batch($slideEl, {
				start: '15% bottom',
				onEnter: () => onEnter(),
				onEnterBack: () => onEnter(),
				onLeave: () => onLeave(),
				onLeaveBack: () => onLeave(),
			});
		};
		onViewHandler();
	}

	return null;
}

export default ShowcaseHero;
